<template>
  <div>
    <myHeader></myHeader>
  <img src="../../../static/img/beijing3.png" style="padding-top:65px" height="250px" width="100%">
    <div class="page_content">
      <div class="page_content_left">
        <div style="background-color: #4c9cff;font-size: 18px;color:#ffffff">
          公示公告//
          <span>Notice of publicity</span>
        </div>
        <div @click="pageTurn('/publicNotices/basicInfo')">基本信息公开</div>
        <div
          style="background-color: #b1cbff;color:#ffffff"
          @click="pageTurn('/publicNotices/bidNotices')"
        >人保招投标公示</div>
      </div>
      <div class="page_content_right">
        <span class="page_content_right_title">人保招投标公示{{childTitle}}</span>
        <div class="page_content_right_items" v-show="!detailShow">
          <div class="page_content_right_item" v-for="(item ,index) in bidNoticesData" :key="index">
            <a style="font-size: 16px" @click="showDetail(item)">{{index+1}}.{{item.title}}</a>
            <p style="font-size: 14px;color: #999999;">{{item.time}}</p>
          </div>
        </div>
        <el-pagination
          class="paginationStyle"
          v-show="!detailShow"
          background
          layout="prev,pager,next,jumper,->,total, slot"
          prev-text="上一页"
          next-text="下一页"
          :page-size="3"
          :total="50"
        ></el-pagination>
        <div class="page_content_detail" v-show="detailShow">
          <span>{{detailData.title}}</span>
          <p>{{detailData.text}}</p>
        </div>
      </div>
    </div>
    <myFooter></myFooter>
  </div>
</template>

<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import tools from "../../tools/tools";

export default {
  name: "bidNotices",
  data() {
    return {
      bidNoticesData: [
        {
          title: "标书2011公示",
          time: "2011年11月11日",
          text:
            "投标书（bidding documents ）是指投标单位按照招标书的条件和要求，向招标单位提交的报价并填具标单的文书。它要求密封后邮寄或派专人送到招标单位，故又称标函。它是投标单位在充分领会招标文件，进行现场实地考察和调查的基础上所编制的投标文书，是对招标公告提出的要求的响应和承诺，并同时提出具体的标价及有关事项来竞争中标。投标书是招标工作时甲乙双方都要承认遵守的具有法律效应的文件，因此逻辑性要强，不能前后矛盾，模棱两可，用语要精练，要简短，对政策法规的准确理解与执行，有利于标书制作者剔除歧视性条款，是对甲方（采购方或发包工程方）“出钱想买什么就买什么”传统观念的强力阻击。"
        },
        { title: "标书2012公示", time: "2012年11月11日", text: "" },
        { title: "标书2013公示", time: "2013年11月11日", text: "" },
        { title: "标书2014公示", time: "2014年11月11日", text: "" },
        { title: "标书2015公示", time: "2015年11月11日", text: "" },
        { title: "标书2016公示", time: "2016年11月11日", text: "" },
        { title: "标书2017公示", time: "2017年11月11日", text: "" },
        { title: "标书2018公示", time: "2018年11月11日", text: "" }
      ],
      detailShow: false,
      detailData: {},
      childTitle: ""
    };
  },
  created() {
    tools.menu();
    if (this.$route.params.title) {
      let title = this.$route.params.title;
      for (let i = 0; i < this.bidNoticesData.length; i += 1) {
        if (title == this.bidNoticesData[i].title) {
          this.detailData = this.bidNoticesData[i];
          this.detailShow = true;
          this.childTitle = ">>" + title;
          return;
        }
      }
    }
  },
  methods: {
    pageTurn: function(path) {
      this.$router.push({ path: path });
      this.detailShow = false;
      this.childTitle = "";
    },
    showDetail: function(item) {
      this.detailShow = true;
      this.detailData = item;
      this.childTitle = ">>" + item.title;
    }
  },
  components: {
    myHeader: Header,
    myFooter: Footer
  }
};
</script>

<style scoped>
.page_content {
  display: flex;
  width: 1200px;
 min-height: 900px;
  margin: 0 0 0 calc(50% - 600px);
}

.page_content_left {
  margin-top: 60px;
  height: 153px;
  width: 300px;
  border-bottom: solid 1px #b3cdff;
  border-left: solid 1px #b3cdff;
  border-right: solid 1px #b3cdff;
  border-radius: 5px;
  overflow: hidden;
}

.page_content_left > div {
  padding-left: 48px;
  height: 50px;
  line-height: 50px;
  border-top: solid 1px #b3cdff;
  color: #b1cbff;
  letter-spacing: 1px;
  cursor: pointer;
}

.page_content_left > div > span {
  font-size: 12px;
}

.page_content_right {
  padding-left: 38px;
  padding-top: 119px;
  width: 850px;
}

.page_content_right_title {
  display: block;
  padding-bottom: 20px;
  border-bottom: 1px solid #4c9cff;
  font-size: 16px;
  letter-spacing: 1px;
  color: #60646c;
}

.page_content_right_items {
  display: flex;
  flex-wrap: wrap;
}

.page_content_right_item {
  padding: 0 20px;
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.page_content_right_item:nth-child(2n) {
  background-color: #eeeeee;
  opacity: 0.8;
}
.page_content_right_item:nth-child(2n + 1) {
  background-color: #eeeeee;
  opacity: 0.4;
}
.paginationStyle {
  padding: 45px 0 45px 200px;
}

.page_content_detail {
  width: 100%;
}
.page_content_detail > span {
  display: block;
  height: 80px;
  line-height: 80px;
  text-align: center;
  font-size: 26px;
  letter-spacing: 2px;
  color: #4c9cff;
}
.page_content_detail > p {
  width: 90%;
  padding: 0 5% 80px 5%;
  text-indent: 40px;
  color: #7b7b7b;
}
</style>




