<template>
  <div style="width:100%;">
    <div class="header_items">
      <div class="company_name">润通保险经纪</div>
      <div
        v-for="(item,index) in headerItem"
        :key="index"
        class="item_move"
        :class="{item_moveUp:item.itemUpActive,item_moveDown: item.itemDownActive}"
        @mouseover="itemForceON(index)"
        @click="pageTurn(item.path)"
      >
        <div class="header_item1">{{item.name}}</div>
        <div class="header_item2">{{item.name}}</div>
      </div>
    </div>
    <div :class="{child_items:childItemShow}" :style="leftDistance">
      <a
        class="child_item"
        v-for="(item,index) in childItem "
        :key="index"
        href="javascript:;"
        @click="pageTurn(item.path)"
      >{{item.name}}</a>
    </div>

  </div>
</template>
<script>
// import tools from "../tools/tools"
export default {
  name: "headerArea",
  data() {
    return {
      leftDistance: "",
      childItemShow: false,
      childItem: [],
      headerItem: [
        {
          name: "首页",
          itemUpActive: false,
          itemDownActive: false,
          path: "/"
        },
        {
          name: "关于我们",
          itemUpActive: false,
          itemDownActive: false,
          path: "/aboutUs/companyInfo",
          childItem: [
            {
              name: "公司介绍",
              path: "/aboutUs/companyInfo"
            },
            {
              name: "领导致辞",
              path: "/aboutUs/leaderSpeech"
            },
            {
              name: "组织机构",
              path: "/aboutUs/organization"
            },
            {
              name: "企业文化",
              path: "/aboutUs/corporateCulture"
            },
            {
              name: "荣耀资质",
              path: "/aboutUs/gloryQualification"
            }
          ]
        },
        {
          name: "资讯中心",
          itemUpActive: false,
          itemDownActive: false,
          path: "/infoCenter/policyLow",
          childItem: [
            {
              name: "政策法规",
              path: "/infoCenter/policyLow"
            },
            {
              name: "视频新闻",
              path: "/infoCenter/videoNews"
            },
            {
              name: "文体活动",
              path: "/infoCenter/clubActivity"
            }
          ]
        },
        {
          name: "公告公示",
          itemUpActive: false,
          itemDownActive: false,
          path: "/publicNotices/basicInfo",
          childItem: [
            {
              name: "基本信息公开",
              path: "/publicNotices/basicInfo"
            },
            {
              name: "人保招投标公示",
              path: "/publicNotices/bidNotices"
            }
          ]
        },

        {
          name: "明星产品",
          itemUpActive: false,
          itemDownActive: false,
          path: "/starProduct"
        },
        {
          name: "党建群团",
          itemUpActive: false,
          itemDownActive: false,
          path: "/politicalParty/partyConstruction",
          childItem: [
            {
              name: "党的建设",
              path: "/politicalParty/partyConstruction"
            },
            {
              name: "纪检工作",
              path: "/politicalParty/inspectDiscipline"
            },
            {
              name: "创先争优",
              path: "/politicalParty/forPriority"
            },
            {
              name: "文明创建",
              path: "/politicalParty/createCivilization"
            },
            {
              name: "四风举报",
              path: "/politicalParty/reportBadness"
            }
          ]
        },
        {
          name: "风险咨询",
          itemUpActive: false,
          itemDownActive: false,
          path: "/riskConsult"
        }
      ]
    };
  },
  methods: {
    itemForceON: function(index) {
      this.headerItem[index].itemUpActive = false;
      this.headerItem[index].itemDownActive = true;
      this.childItem = this.headerItem[index].childItem
        ? this.headerItem[index].childItem
        : {};
      this.childItemShow = this.headerItem[index].childItem ? true : false;
      this.leftDistance =
        "right:calc(50% - 450px + "+ 100 * (5-index)+"px)";
        console.log(this.leftDistance)
      for (let i = 0; i < this.headerItem.length; i += 1) {
        if (i != index) {
          if (this.headerItem[i].itemDownActive) {
            this.headerItem[i].itemUpActive = true;
            this.headerItem[i].itemDownActive = false;
          }
        }
      }
    },
    pageTurn: function(path) {
      this.$router.push({ path: path });
    }
  }
};
</script>

<style>
.header_items {
  width: 100%;
  height: 65px;
  display: flex;
  justify-content: center;
  overflow: hidden;
  z-index: 200;
  position: absolute;
  top: 0px;
  background-color: white;
}

.child_items {
  /* width:100%; */
  float:right;
  height: 45px;
  background-color:#4d9ffc;
  position: absolute;
  top: 65px;
  z-index: 100;
  box-shadow: -1px 1px 10px 1px #7d92aa;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;  
  animation: dropDown 0.3s;
  -webkit-animation: dropDown 0.3s;
  animation-timing-function: ease-in;
  -webkit-animation-timing-function: ease-in;
  animation-fill-modeforwards: forwards;
  -webkit-animation-fill-modeforwards: forwards;
}
.child_items>a{
  text-decoration:none;
  color:white;
  float: right
}
.child_items>a:hover{
  color:blue
}

.child_item {
  display: inline-block;
  text-decoration: none;
  height: 45px;
  line-height: 45px;
  padding: 0 10px;
}

.header_img {
  width: 100vw;
}

.header_item1 {
  margin:0px 1px 0px 1px;
  width: 100px;
  height: 66px;
  /* min-width: 60px; */
  line-height: 66px;
  text-align: center;
  background-color: #4d9ffc;
  color: #fff;
  /* border-radius: 5px; */
}

.header_item2 {
    margin:0px 1px 0px 1px;
  width: 100px;
  height: 66px;
  /* min-width: 60px; */
  line-height: 65px;
  text-align: center;
  font-size: 18px;
  color: #707384;
}

.company_name {
  width: 200px;
  height: 65px;
  min-width: 200px;
  line-height: 65px;
  text-align: center;
  margin-right: 20px;
  font-family: FZHZGBJW--GB1-0;
  font-size: 30px;
  color: #4c9cff;
}

.item_move {
  position: relative;
  top: -66px;
  cursor: pointer;
}

.item_moveUp {
  top: -66px;
  animation: moveUp 0.3s;
  -webkit-animation: moveUp 0.3s;
  animation-timing-function: ease-in;
  -webkit-animation-timing-function: ease-in;
  animation-fill-modeforwards: forwards;
  -webkit-animation-fill-modeforwards: forwards;
}

.item_moveDown {
  top: -1px;
  animation: moveDown 0.3s;
  -webkit-animation: moveDown 0.3s;
  animation-timing-function: ease-in;
  -webkit-animation-timing-function: ease-in;
  animation-fill-modeforwards: forwards;
  -webkit-animation-fill-modeforwards: forwards;
}

@keyframes moveDown {
  0% {
    top: -65px;
  }
  100% {
    top: 0px;
  }
}

@-webkit-keyframes moveDown {
  0% {
    top: -65px;
  }
  100% {
    top: 0px;
  }
}

@keyframes moveUp {
  0% {
    top: 0px;
  }
  100% {
    top: -65px;
  }
}

@-webkit-keyframes moveUp {
  0% {
    top: 0px;
  }
  100% {
    top: -65px;
  }
}

@keyframes dropDown {
  0% {
    top: 40px;

  }
  100% {
    top: 65px;

  }
}

@-webkit-keyframes dropDown {
  0% {
    top: 40px;

  }
  100% {
    top: 65px;
  }
}
</style>



