<template>
  <div>
    <myHeader></myHeader>
    <div v-for="(item,index) in showModul" :key="index" style="overflow:hidden">
      <img :src="item.src" width="100%" height="100%" @click="detailShow(index)" v-show="item.show">
      <div v-show="item.childShow" class="productInfo">
        <div v-for="n in 12" :key="n" :class="['productItem'+n]">
          <img :src="item.childSrc[n-1]" width="100%" height="100%">
        </div>
        <!-- <div class="productItem1"></div>
        <div class="productItem2"></div>
        <div class="productItem3"></div>
        <div class="productItem4"></div>
        <div class="productItem5"></div>
        <div class="productItem6"></div>
        <div class="productItem7"></div>
        <div class="productItem8"></div>
        <div class="productItem9"></div>
        <div class="productItem10"></div>
        <div class="productItem11"></div>
        <div class="productItem12"></div>-->
      </div>
    </div>
    <!-- <img src="./static/img/starProduct1.png" style="padding-top:85px;width: 100% ">

    <img src="../../static/img/starProduct2.png" width="100%">
    <img src="../../static/img/starProduct3.png" width="100%">
    <img src="../../static/img/starProduct4.png" width="100%">-->
    <myFooter></myFooter>
  </div>
</template>

<script>
import Header from "../components/Header";
import Footer from "../components/Footer";
import tools from "../tools/tools";

export default {
  name: "starProduct",
  data() {
    return {
      showModul: [
        {
          show: true,
          childShow: false,
          src: "./static/img/starProduct1.png",
          childSrc: [
            "./static/img/mingxing01.png",
            "./static/img/mingxing02.png",
            "./static/img/mingxing03.png",
            "./static/img/mingxing04.png",
            "./static/img/mingxing05.png",
            "./static/img/mingxing06.png",
            "./static/img/mingxing07.png",
            "./static/img/mingxing08.png",
            "./static/img/mingxing09.png",
            "./static/img/mingxing10.png",
            "./static/img/mingxing11.png",
            "./static/img/mingxing12.png"
          ]
        },
        {
          show: true,
          childShow: false,
          src: "./static/img/starProduct2.png",
          childSrc: [
            "./static/img/mingxing01.png",
            "./static/img/mingxing02.png",
            "./static/img/mingxing03.png",
            "./static/img/mingxing04.png",
            "./static/img/mingxing05.png",
            "./static/img/mingxing06.png",
            "./static/img/mingxing07.png",
            "./static/img/mingxing08.png",
            "./static/img/mingxing09.png",
            "./static/img/mingxing10.png",
            "./static/img/mingxing11.png",
            "./static/img/mingxing12.png"
          ]
        },
        {
          show: true,
          childShow: false,
          src: "./static/img/starProduct3.png",
          childSrc: [
            "./static/img/mingxing01.png",
            "./static/img/mingxing02.png",
            "./static/img/mingxing03.png",
            "./static/img/mingxing04.png",
            "./static/img/mingxing05.png",
            "./static/img/mingxing06.png",
            "./static/img/mingxing07.png",
            "./static/img/mingxing08.png",
            "./static/img/mingxing09.png",
            "./static/img/mingxing10.png",
            "./static/img/mingxing11.png",
            "./static/img/mingxing12.png"
          ]
        },
        {
          show: true,
          childShow: false,
          src: "./static/img/starProduct4.png",
          childSrc: [
            "./static/img/mingxing01.png",
            "./static/img/mingxing02.png",
            "./static/img/mingxing03.png",
            "./static/img/mingxing04.png",
            "./static/img/mingxing05.png",
            "./static/img/mingxing06.png",
            "./static/img/mingxing07.png",
            "./static/img/mingxing08.png",
            "./static/img/mingxing09.png",
            "./static/img/mingxing10.png",
            "./static/img/mingxing11.png",
            "./static/img/mingxing12.png"
          ]
        }
      ]
    };
  },
  created() {
    tools.menu();
  },
  methods: {
    detailShow: function(index) {
      var showStatus = this.showModul[index].childShow;
      this.showModul[index].childShow = !showStatus;
      for (let i = 0; i < this.showModul.length; i += 1) {
        if (i != index) {
          this.showModul[i].childShow = false;
        }
      }
    }
  },
  components: {
    myHeader: Header,
    myFooter: Footer
  }
};
</script>

<style scoped>
/* .productInfo > div {
  background-color: blue;
} */
.productItem1 {
  grid-area: item1;
}
.productItem2 {
  grid-area: item2;
}
.productItem3 {
  grid-area: item3;
}
.productItem4 {
  grid-area: item4;
}
.productItem5 {
  grid-area: item5;
}
.productItem6 {
  grid-area: item6;
}
.productItem7 {
  grid-area: item7;
}
.productItem8 {
  grid-area: item8;
}
.productItem9 {
  grid-area: item9;
}
.productItem10 {
  grid-area: item10;
}
.productItem11 {
  grid-area: item11;
}
.productItem12 {
  grid-area: item12;
}
.productInfo {
  width: 1200px;
  height: 730px;
  margin: 20px calc(50% - 600px);
  /* padding-bottom: 40px; */

  display: grid;
  /*划分列*/
  grid-template-columns: 320px 320px 1fr 1fr;
  /*划分行*/
  grid-template-rows: 1fr 1fr 30px 1fr 1fr;
  /*行列间距*/
  /* grid-column-gap: 10px;
  grid-row-gap: 10px; */
  /*内容位置 居中是按网格数计算居中不是按长度*/
  /* justify-items: center; */
  /* align-items: center; */
  grid-template-areas: "item1 item2 item3 item4" "item1 item2 item5 item6" ". . . ." "item7 item8 item9 item10" "item7 item8 item11 item12";
}
</style>



