<template>
  <div>
    <myHeader></myHeader>
   <img src="../../../static/img/beijing3.png" style="padding-top:65px" height="250px" width="100%">
    <div class="page_content">
      <div class="page_content_left">
        <div style="background-color: #4c9cff;font-size: 18px;color:#ffffff">
          公示公告//
          <span>Notice of publicity</span>
        </div>
        <div
          style="background-color: #b1cbff;color:#ffffff"
          @click="pageTurn('/publicNotices/basicInfo')"
        >基本信息公开</div>
        <div @click="pageTurn('/publicNotices/bidNotices')">人保招投标公示</div>
      </div>
      <div class="page_content_right">
        <span class="page_content_right_title">基本信息公开</span>
        <div class="page_content_detail">
          <span>基本信息公开</span>
          <p>投标书（bidding documents ）是指投标单位按照招标书的条件和要求，向招标单位提交的报价并填具标单的文书。它要求密封后邮寄或派专人送到招标单位，故又称标函。</p>
        </div>
      </div>
    </div>
    <myFooter></myFooter>
  </div>
</template>

<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import tools from "../../tools/tools";

export default {
  name: "basicInfo",
  data() {
    return {};
  },
  created() {
    tools.menu();
  },
  methods: {
    pageTurn: function(path) {
      this.$router.push({ path: path });
    }
  },
  components: {
    myHeader: Header,
    myFooter: Footer
  }
};
</script>

<style scoped>
.page_content {
  display: flex;
  width: 1200px;
  min-height: 900px;
  margin: 0 0 0 calc(50% - 600px);
}

.page_content_left {
  margin-top: 60px;
  height: 153px;
  width: 300px;
  border-bottom: solid 1px #b3cdff;
  border-left: solid 1px #b3cdff;
  border-right: solid 1px #b3cdff;
  border-radius: 5px;
  overflow: hidden;
}

.page_content_left > div {
  padding-left: 48px;
  height: 50px;
  line-height: 50px;
  border-top: solid 1px #b3cdff;
  color: #b1cbff;
  letter-spacing: 1px;
  cursor: pointer;
}

.page_content_left > div > span {
  font-size: 12px;
}

.page_content_right {
  padding-left: 38px;
  padding-top: 119px;
  width: 850px;
}

.page_content_right_title {
  display: block;
  padding-bottom: 20px;
  border-bottom: 1px solid #4c9cff;
  font-size: 16px;
  letter-spacing: 1px;
  color: #60646c;
}

.page_content_detail {
  width: 100%;
}
.page_content_detail > span {
  display: block;
  height: 80px;
  line-height: 80px;
  text-align: center;
  font-size: 26px;
  letter-spacing: 2px;
  color: #4c9cff;
}
.page_content_detail > p {
  width: 90%;
  padding: 0 5% 80px 5%;
  text-indent: 40px;
  color: #7b7b7b;
}
</style>




