<template>
  <div>
    <myHeader></myHeader>
    <img src="../../../static/img/beijing3.png" style="padding-top:65px" height="250px" width="100%">
    <div class="page_content">
      <div class="page_content_left">
        <div style="background-color: #4c9cff;font-size: 18px;color:#ffffff">
          资讯中心/
          <span>Information Center</span>
        </div>
        <div @click="pageTurn('/infoCenter/policyLow')">政策法规</div>
        <div
          @click="pageTurn('/infoCenter/videoNews')"
          style="background-color: #b1cbff;color:#ffffff"
        >视频新闻</div>
        <div @click="pageTurn('/infoCenter/clubActivity')">文体活动</div>
      </div>

      <div class="page_content_right" v-show="!detailShow">
        <p class="page_content_right_title">视频新闻</p>
        <div class="page_content_right_items">
          <div class="page_content_right_item" v-for="item in videoNews" :key="item.title">
            <video width="250px" height="140px" controls>
              <source :src="item.src" type="video/mp4">
            </video>
            <a @click="detailVideo(item)">{{item.title }}</a>
          </div>
        </div>
        <el-pagination
          class="paginationStyle"
          background
          layout="prev,pager,next,jumper,->,total, slot"
          prev-text="上一页"
          next-text="下一页"
          :page-size="3"
          :total="50"
        ></el-pagination>
      </div>
      <div class="page_content_right" v-show="detailShow">
        <p class="page_content_right_title">视频新闻{{childTitle}}</p>
        <div class="detail_video" v-for="item in detailData" :key="item.title">
          <span>{{item.title}}</span>
          <!-- <video controls style="padding:20px;width:810px;height: 580px"> -->
          <video controls>
            <source :src="item.src" type="video/mp4">
          </video>
          <p>{{item.text}}</p>
        </div>
      </div>
    </div>
    <myFooter></myFooter>
  </div>
</template>

<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import tools from "../../tools/tools";

export default {
  name: "videoNews",
  data() {
    return {
      detailShow: false,
      detailData: [],
      childTitle: "",
      videoNews: [
        {
          title: "3wtv新闻联播",
          src: " ./static/video/video1.mp4",
          text: "3wtv新闻联播《【到群众中去·整改进行时】 保险业..."
        },
        {
          title: "广西新闻",
          src: " ./static/video/video2.mp4",
          text: "[广西新闻]提待遇强机制广西进一步完善工伤 保险政策..."
        },
        {
          title: "保险业对外开放",
          src: " ./static/video/video1.mp4",
          text: "保险业对外开放实现新突破加快开放势在必 行..."
        },
        {
          title: "3wtv1",
          src: " ./static/video/video1.mp4",
          text: "3wtv新闻联播《【到群众中去·整改进行时】 保险业..."
        },
        {
          title: "3wtv2",
          src: " ./static/video/video1.mp4",
          text: "[广西新闻]提待遇强机制广西进一步完善工伤 保险政策..."
        },
        {
          title: "3wtv3",
          src: " ./static/video/video1.mp4",
          text: "保险业对外开放实现新突破加快开放势在必 行..."
        },
        {
          title: "3wtv4",
          src: " ./static/video/video1.mp4",
          text: "3wtv新闻联播《【到群众中去·整改进行时】 保险业..."
        },
        {
          title: "3wtv5",
          src: " ./static/video/video1.mp4",
          text: "[广西新闻]提待遇强机制广西进一步完善工伤 保险政策..."
        },
        {
          title: "3wtv6",
          src: " ./static/video/video1.mp4",
          text: "保险业对外开放实现新突破加快开放势在必 行..."
        }
      ]
    };
  },
  created() {
    tools.menu();
    if (this.$route.params.title) {
      let title = this.$route.params.title;
      for (let i = 0; i < this.videoNews.length; i += 1) {
        if (title == this.videoNews[i].title) {
          this.detailData[0] = this.videoNews[i];
          this.detailShow = true;
          this.childTitle = ">>" + title;
          return;
        }
      }
    }
  },
  methods: {
    detailVideo: function(item) {
      this.detailShow = true;
      this.detailData[0] = item;
      console.log(this.detailData);
      this.childTitle = ">>" + item.title;
    },
    pageTurn: function(path) {
      this.$router.push({ path: path });
      this.detailShow = false;
    }
  },
  components: {
    myHeader: Header,
    myFooter: Footer
  }
};
</script>

<style scoped>
.page_content {
  display: flex;
  width: 1200px;
  min-height: 900px;
  margin: 0 0 0 calc(50% - 600px);
}

.page_content_left {
  margin-top: 60px;
  height: 204px;
  width: 300px;
  border-bottom: solid 1px #b3cdff;
  border-left: solid 1px #b3cdff;
  border-right: solid 1px #b3cdff;
  border-radius: 5px;
  overflow: hidden;
}

.page_content_left > div {
  padding-left: 48px;
  height: 50px;
  line-height: 50px;
  border-top: solid 1px #b3cdff;
  color: #b1cbff;
  letter-spacing: 1px;
  cursor: pointer;
}

.page_content_left > div > span {
  font-size: 12px;
}

.page_content_right {
  padding-left: 38px;
  padding-top: 119px;
  width: 850px;
}

.page_content_right_title {
  padding-bottom: 20px;
  border-bottom: 1px solid #4c9cff;
  font-size: 16px;
  letter-spacing: 1px;
  color: #60646c;
}

.page_content_right_items {
  display: flex;
  flex-wrap: wrap;
}

.page_content_right_item {
  padding: 20px 10px 0 10px;
  width: 260px;
  height: 190px;
}

.page_content_right_item > a {
  display: block;
  text-align: center;
  margin-top: 10px;
  font-size: 16px;
}
.detail_video > span {
  display: block;
  width: 100%;
  height: 66px;
  line-height: 66px;
  text-align: center;
  font-size: 24px;
}
.detail_video > p {
  padding: 15px 5% 80px 5%;
  width: 90%;
  text-indent: 40px;
}
.detail_video > video {
  padding: 0 10%;
  width: 80%;
}

.paginationStyle {
  padding: 45px 50px 45px 150px;
}
</style>



