<template>
  <div>
    <myHeader></myHeader>
   <img src="../../../static/img/beijing3.png" style="padding-top:65px" height="250px" width="100%">
    <div class="page_content">
      <div class="page_content_left">
        <div style="background-color: #4c9cff;font-size: 18px;color:#ffffff">
          资讯中心/
          <span>Information Center</span>
        </div>
        <div @click="pageTurn('/infoCenter/policyLow')">政策法规</div>
        <div @click="pageTurn('/infoCenter/videoNews')">视频新闻</div>
        <div
          @click="pageTurn('/infoCenter/clubActivity')"
          style="background-color: #b1cbff;color:#ffffff"
        >文体活动</div>
      </div>
      <div class="page_content_right">
        <span class="page_content_right_title">文体活动{{childTitle}}</span>
        <div class="page_content_right_items">
          <div
            class="page_content_right_item"
            v-show="!detailShow"
            v-for="(item,index) in clubActivityData"
            :key="item.title"
          >
            <a style="font-size: 16px;" @click="showDetail(item)">{{index+1}}.{{item.title}}</a>
            <p style="font-size: 14px;color: #999999;">{{item.time}}</p>
          </div>
        </div>
        <el-pagination
          class="paginationStyle"
          v-show="!detailShow"
          background
          layout="prev,pager,next,jumper,->,total, slot"
          prev-text="上一页"
          next-text="下一页"
          :page-size="3"
          :total="50"
        ></el-pagination>
        <div class="page_content_detail" v-show="detailShow">
          <span>{{detailData.title}}</span>
          <p>{{detailData.text}}</p>
        </div>
      </div>
    </div>
    <myFooter></myFooter>
  </div>
</template>

<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import tools from "../../tools/tools";

export default {
  name: "clubActivity",
  data() {
    return {
      clubActivityData: [
        {
          title: "文体活动2011",
          time: "2018-02-11",
          text:
            "开幕式活动设传统文化项目体验及竞技活动，传统文化体验项目分为弄堂游戏、中国象棋、制作香包等10个体验项目，竞技活动分为“射五毒”、“浑水摸鱼”、赛龙舟三个项目。活动中，全体队员齐心协力、积极向上，发扬了顽强拼搏的精神，充分展示了绿舟教职员工勇于拼搏、团结协作的精神风貌。"
        },
        {
          title: "文体活动2012",
          time: "2018-02-12",
          text:
            "开幕式活动设传统文化项目体验及竞技活动，传统文化体验项目分为弄堂游戏、中国象棋、制作香包等10个体验项目，竞技活动分为“射五毒”、“浑水摸鱼”、赛龙舟三个项目。活动中，全体队员齐心协力、积极向上，发扬了顽强拼搏的精神，充分展示了绿舟教职员工勇于拼搏、团结协作的精神风貌。"
        },
        {
          title: "文体活动2013",
          time: "2018-02-13",
          text:
            "开幕式活动设传统文化项目体验及竞技活动，传统文化体验项目分为弄堂游戏、中国象棋、制作香包等10个体验项目，竞技活动分为“射五毒”、“浑水摸鱼”、赛龙舟三个项目。活动中，全体队员齐心协力、积极向上，发扬了顽强拼搏的精神，充分展示了绿舟教职员工勇于拼搏、团结协作的精神风貌。"
        },
        {
          title: "文体活动2014",
          time: "2018-02-14",
          text:
            "开幕式活动设传统文化项目体验及竞技活动，传统文化体验项目分为弄堂游戏、中国象棋、制作香包等10个体验项目，竞技活动分为“射五毒”、“浑水摸鱼”、赛龙舟三个项目。活动中，全体队员齐心协力、积极向上，发扬了顽强拼搏的精神，充分展示了绿舟教职员工勇于拼搏、团结协作的精神风貌。"
        },
        {
          title: "文体活动2015",
          time: "2018-02-15",
          text:
            "开幕式活动设传统文化项目体验及竞技活动，传统文化体验项目分为弄堂游戏、中国象棋、制作香包等10个体验项目，竞技活动分为“射五毒”、“浑水摸鱼”、赛龙舟三个项目。活动中，全体队员齐心协力、积极向上，发扬了顽强拼搏的精神，充分展示了绿舟教职员工勇于拼搏、团结协作的精神风貌。"
        }
      ],
      detailShow: false,
      detailData: {},
      childTitle: ""
    };
  },
  created() {
    tools.menu();
    if (this.$route.params.title) {
      let title = this.$route.params.title;
      for (let i = 0; i < this.clubActivityData.length; i += 1) {
        if (title == this.clubActivityData[i].title) {
          this.detailData = this.clubActivityData[i];
          this.detailShow = true;
          this.childTitle = ">>" + title;
          return;
        }
      }
    }
  },
  methods: {
    pageTurn: function(path) {
      this.$router.push({ path: path });
      this.detailShow = false;
      this.childTitle = "";
    },
    showDetail: function(item) {
      this.detailShow = true;
      this.detailData = item;
      this.childTitle = ">>" + item.title;
    }
  },
  components: {
    myHeader: Header,
    myFooter: Footer
  }
};
</script>

<style scoped>
.page_content {
  display: flex;
  width: 1200px;
  min-height: 900px;
  margin: 0 0 0 calc(50% - 600px);
}

.page_content_left {
  margin-top: 60px;
  height: 204px;
  width: 300px;
  border-bottom: solid 1px #b3cdff;
  border-left: solid 1px #b3cdff;
  border-right: solid 1px #b3cdff;
  border-radius: 5px;
  overflow: hidden;
}

.page_content_left > div {
  padding-left: 48px;
  height: 50px;
  line-height: 50px;
  border-top: solid 1px #b3cdff;
  color: #b1cbff;
  letter-spacing: 1px;
  cursor: pointer;
}

.page_content_left > div > span {
  font-size: 12px;
}

.page_content_right {
  padding-left: 38px;
  padding-top: 119px;
  width: 850px;
}
.page_content_right_title {
  display: block;
  padding-bottom: 20px;
  border-bottom: 1px solid #4c9cff;
  font-size: 16px;
  letter-spacing: 1px;
  color: #60646c;
}

.page_content_right_items {
  display: flex;
  flex-wrap: wrap;
}

.page_content_right_item {
  padding: 0 20px;
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.page_content_right_item:nth-child(2n) {
  background-color: #eeeeee;
  opacity: 0.8;
}
.page_content_right_item:nth-child(2n + 1) {
  background-color: #eeeeee;
  opacity: 0.4;
}
.paginationStyle {
  padding: 45px 0 45px 200px;
}
.page_content_detail {
  width: 100%;
}
.page_content_detail > span {
  display: block;
  height: 80px;
  line-height: 80px;
  text-align: center;
  font-size: 26px;
  letter-spacing: 2px;
  color: #4c9cff;
}
.page_content_detail > p {
  width: 90%;
  padding: 0 5% 80px 5%;
  text-indent: 40px;
  color: #7b7b7b;
}
</style>



