import Vue from 'vue'
import Router from 'vue-router'
import homePage from '@/pages/homePage'

import companyInfo from '@/pages/aboutUs/companyInfo'
import leaderSpeech from '@/pages/aboutUs/leaderSpeech'
import organization from '@/pages/aboutUs/organization'
import corporateCulture from '@/pages/aboutUs/corporateCulture'
import gloryQualification from '@/pages/aboutUs/gloryQualification'

import policyLow from '@/pages/infoCenter/policyLow'
import videoNews from '@/pages/infoCenter/videoNews'
import clubActivity from '@/pages/infoCenter/clubActivity'

import bidNotices from '@/pages/publicNotices/bidNotices'
import basicInfo from '@/pages/publicNotices/basicInfo'

import starProduct from '@/pages/starProduct'

import partyConstruction from '@/pages/politicalParty/partyConstruction'
import inspectDiscipline from '@/pages/politicalParty/inspectDiscipline'
import forPriority from '@/pages/politicalParty/forPriority'
import createCivilization from '@/pages/politicalParty/createCivilization'
import reportBadness from '@/pages/politicalParty/reportBadness'

import riskConsult from '@/pages/riskConsult'

Vue.use(Router)
export default new Router({
  routes: [
    // 首页
    {
      path: '/',
      name: 'homePage',
      component: homePage
    },

    // 关于我们
    {
      path: '/aboutUs/companyInfo',
      name: 'companyInfo',
      component: companyInfo
    },
    {
      path: '/aboutUs/leaderSpeech',
      name: 'leaderSpeech',
      component: leaderSpeech
    },
    {
      path: '/aboutUs/organization',
      name: 'organization',
      component: organization
    },
    {
      path: '/aboutUs/corporateCulture',
      name: 'corporateCulture',
      component: corporateCulture
    },
    {
      path: '/aboutUs/gloryQualification',
      name: 'gloryQualification',
      component: gloryQualification
    },
    // 资讯中心
    {
      path: '/infoCenter/policyLow',
      name: 'policyLow',
      component: policyLow
    },
    {
      path: '/infoCenter/videoNews',
      name: 'videoNews',
      component: videoNews
    },
    {
      path: '/infoCenter/clubActivity',
      name: 'clubActivity',
      component: clubActivity
    },
    // 公示公告
    {
      path: '/publicNotices/bidNotices',
      name: 'bidNotices',
      component: bidNotices
    },
    {
      path: '/publicNotices/basicInfo',
      name: 'basicInfo',
      component: basicInfo
    },
    // 明星产品
    {
      path: '/starProduct',
      name: 'starProduct',
      component: starProduct
    },

    //党建群团
    {
      path: '/politicalParty/partyConstruction',
      name: 'partyConstruction',
      component: partyConstruction
    },
    {
      path: '/politicalParty/inspectDiscipline',
      name: 'inspectDiscipline',
      component: inspectDiscipline
    },
    {
      path: '/politicalParty/forPriority',
      name: 'forPriority',
      component: forPriority
    },
    {
      path: '/politicalParty/createCivilization',
      name: 'createCivilization',
      component: createCivilization
    },
    {
      path: '/politicalParty/reportBadness',
      name: 'reportBadness',
      component: reportBadness
    },
    // 风险咨询
    {
      path: '/riskConsult',
      name: 'riskConsult',
      component: riskConsult
    },
  ]
})



