<template>
  <div>
    <myHeader></myHeader>
    <img src="../../../static/img/beijing3.png" style="padding-top:65px" height="250px" width="100%">
    <div class="page_content">
      <div class="page_content_left">
        <div style="background-color: #4c9cff;font-size: 18px;color:#ffffff">
          资讯中心/
          <span>Information Center</span>
        </div>
        <div
          @click="pageTurn('/infoCenter/policyLow')"
          style="background-color: #b1cbff;color:#ffffff"
        >政策法规</div>
        <div @click="pageTurn('/infoCenter/videoNews')">视频新闻</div>
        <div @click="pageTurn('/infoCenter/clubActivity')">文体活动</div>
      </div>
      <div class="page_content_right">
        <span class="page_content_right_title">政策法规{{childTitle}}</span>
        <div class="page_content_right_items" v-show="!detailShow">
          <div class="page_content_right_item" v-for="(item,index) in policyLowData" :key="index">
            <img :src="item.src">
            <div>
              <span>{{item.title}}</span>
              <p>{{item.text}}</p>
              <a href="javascript:;" @click="showDetail(item)">更多详情>></a>
            </div>
          </div>
        </div>
        <el-pagination
          class="paginationStyle"
          background
          layout="prev,pager,next,jumper,->,total, slot"
          prev-text="上一页"
          next-text="下一页"
          v-show="!detailShow"
          :page-size="3"
          :total="50"
        ></el-pagination>
        <div class="page_content_detail" v-show="detailShow">
          <span>{{detailData.title}}</span>
          <img :src="detailData.src">
          <p>{{detailData.text}}</p>
        </div>
      </div>
    </div>
    <myFooter></myFooter>
  </div>
</template>

<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import tools from "../../tools/tools";

export default {
  name: "policyLow",
  data() {
    return {
      policyLowData: [
        {
          title: "中国保监会关于",
          src: "./static/img/djtz.png",
          text:
            "各财产保险公司：根据《中国保监会关于深化商业车险条款费率管理制度改革的意见》（保监发〔2015〕18号），更好地发挥市场在资源配置中的决定性作用，现就调整部分地区商业车险自主定价范围有..."
        },
        {
          title: "调整部分地区商业车险",
          src: "./static/img/djtz.png",
          text:
            "各财产保险公司：根据《中国保监会关于深化商业车险条款费率管理制度改革的意见》（保监发〔2015〕18号），更好地发挥市场在资源配置中的决定性作用，现就调整部分地区商业车险自主定价范围有..."
        },
        {
          title: "自主定价范围的通知",
          src: "./static/img/djtz.png",
          text:
            "各财产保险公司：根据《中国保监会关于深化商业车险条款费率管理制度改革的意见》（保监发〔2015〕18号），更好地发挥市场在资源配置中的决定性作用，现就调整部分地区商业车险自主定价范围有..."
        }
      ],
      detailShow: false,
      detailData: {},
      childTitle: ""
    };
  },
  created() {
    tools.menu();
    if (this.$route.params.title) {
      let title = this.$route.params.title;
      for (let i = 0; i < this.policyLowData.length; i += 1) {
        if (title == this.policyLowData[i].title) {
          this.detailData = this.policyLowData[i];
          this.detailShow = true;
          this.childTitle = ">>" + title;
          return;
        }
      }
    }
  },
  methods: {
    pageTurn: function(path) {
      this.$router.push({ path: path });
      this.detailShow = false;
      this.childTitle = "";
    },
    showDetail: function(item) {
      this.detailShow = true;
      this.detailData = item;
      this.childTitle = ">>" + item.title;
    }
  },
  components: {
    myHeader: Header,
    myFooter: Footer
  }
};
</script>

<style scoped>
.page_content {
  display: flex;
  width: 1200px;
  min-height: 900px;
  margin: 0 0 0 calc(50% - 600px);
}

.page_content_left {
  margin-top: 60px;
  height: 204px;
  width: 300px;
  border-bottom: solid 1px #b3cdff;
  border-left: solid 1px #b3cdff;
  border-right: solid 1px #b3cdff;
  border-radius: 5px;
  overflow: hidden;
}

.page_content_left > div {
  padding-left: 48px;
  height: 50px;
  line-height: 50px;
  border-top: solid 1px #b3cdff;
  color: #b1cbff;
  letter-spacing: 1px;
  cursor: pointer;
}

.page_content_left > div > span {
  font-size: 12px;
}

.page_content_right {
  padding-left: 38px;
  padding-top: 119px;
  width: 850px;
}
.page_content_detail p {
  text-indent: 40px;
}
.page_content_right_title {
  display: block;
  padding-bottom: 20px;
  border-bottom: 1px solid #4c9cff;
  font-size: 16px;
  letter-spacing: 1px;
  color: #60646c;
}

.page_content_right_items {
  display: flex;
  flex-wrap: wrap;
}

.page_content_right_item {
  padding-top: 20px;
  width: 100%;
  height: 240px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page_content_right_item > img {
  width: 400px;
}

.page_content_right_item > div {
  padding-left: 24px;
  height: 230px;
}

.page_content_right_item > div > span {
  display: block;
  height: 66px;
  font-size: 24px;
  color: #4f4f4f;
}

.page_content_right_item > div > p {
  margin-top: 30px;
  font-size: 16px;
  color: #999999;
}

.page_content_right_item > div > a {
  display: block;
  margin-top: 19px;
  text-decoration: underline;
  font-size: 16px;
}
/* 分页样式 */
.paginationStyle {
  padding: 45px 0 45px 200px;
}
.page_content_detail {
  width: 100%;
}
.page_content_detail > span {
  display: block;
  height: 66px;
  line-height: 66px;
  text-align: center;
  font-size: 24px;
  color: #4f4f4f;
}
.page_content_detail > img {
  width: 80%;
  padding: 0px 10%;
}
.page_content_detail > p {
  width: 90%;
  padding: 0 5% 80px 5%;
  text-indent: 40px;
}
</style>



