<template>
  <div>
    <myHeader></myHeader>
 <img src="../../../static/img/beijing3.png" style="padding-top:65px" height="250px" width="100%">
    <div class="page_content">
      <div class="page_content_left">
        <div style="background-color: #4c9cff;font-size: 18px;color:#ffffff">
          关于我们/
          <span>About us</span>
        </div>
        <div @click="pageTurn('/aboutUs/companyInfo')">公司介绍</div>
        <div @click="pageTurn('/aboutUs/leaderSpeech')">领导致辞</div>
        <div @click="pageTurn('/aboutUs/organization')">组织机构</div>
        <div @click="pageTurn('/aboutUs/corporateCulture')">企业文化</div>
        <div
          @click="pageTurn('/aboutUs/gloryQualification')"
          style="background-color: #b1cbff;color:#ffffff"
        >荣誉资质</div>
      </div>
      <div class="page_content_right">
        <span class="page_content_right_title">荣耀资质</span>
      </div>
    </div>
    <myFooter></myFooter>
  </div>
</template>

<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import tools from "../../tools/tools";
export default {
  name: "leaderSpeech",
  data() {
    return {};
  },
  created() {
    tools.menu();
  },
  methods: {
    pageTurn: function(path) {
      this.$router.push({ path: path });
    }
  },
  components: {
    myHeader: Header,
    myFooter: Footer
  }
};
</script>

<style scoped>
.page_content {
  display: flex;
  width: 1200px;
 min-height: 900px;
  margin: 0 0 0 calc(50% - 600px);
}

.page_content_left {
  margin-top: 60px;
  height: 305px;
  width: 300px;
  border-bottom: solid 1px #b3cdff;
  border-left: solid 1px #b3cdff;
  border-right: solid 1px #b3cdff;
  border-radius: 5px;
  overflow: hidden;
}

.page_content_left > div {
  padding-left: 48px;
  height: 50px;
  line-height: 50px;
  border-top: solid 1px #b3cdff;
  color: #b1cbff;
  letter-spacing: 1px;
  cursor: pointer;
}

.page_content_left > div > span {
  font-size: 12px;
}

.page_content_right {
  padding-left: 38px;
  padding-top: 119px;
  width: 850px;
}

.page_content_right_title {
  display: block;
  padding-bottom: 20px;
  border-bottom: 1px solid #4c9cff;
  font-size: 16px;
  letter-spacing: 1px;
  color: #60646c;
}
</style>


